import React, { useContext } from "react";
import { Card, ListGroup } from "react-bootstrap";

import "./VotingEventsList.scss";

import { VotingContractContext } from "../../contexts/voting-contract-context";
import { VotingWorkflowStatusEnum } from "../../interfaces/VotingWorkflowStatusEnum";

function VotingEventsList() {
  // Contexts
  const { votingContractEvents } = useContext(VotingContractContext);

  const mappingTypes = {
    VoterRegistered: "Ajout d'un votant",
    VoterUnregistered: "Suppression d'un votant",
    WorkflowStatusChange: "Changement de status",
    ProposalRegistered: "Ajout d'une proposition",
    Voted: "Ajout d'un vote",
    VotesTallied: "Comptabilisation des votes",
  };

  return (
    <ListGroup variant="flush">
      {votingContractEvents.map((votingContractEvent) => {
        let message = "";
        console.log(votingContractEvent)
        switch (votingContractEvent.type) {
          case 'VoterRegistered':
            message = `${ votingContractEvent.data.voterAddress } a été ajouté dans la liste des votants.`;
            break;

          case 'VoterUnregistered':
            message = `${ votingContractEvent.data.voterAddress } a été supprimé de la liste des votants.`;
            break;

          case 'WorkflowStatusChange':
            switch (votingContractEvent.data.newStatus) {
              case VotingWorkflowStatusEnum.RegisteringVoters:
                message = `Une proposition a été ajouté (Proposition n°${ votingContractEvent.data.newStatus }).`;
                break;

              case VotingWorkflowStatusEnum.ProposalsRegistrationStarted:
                message = `La session d'enregistrement des votants vient d'être fermé et la session d'ajout des propositions vient d'ouvrir.`;
                break;

              case VotingWorkflowStatusEnum.ProposalsRegistrationEnded:
                message = `La session d'ajout des propositions vient d'être fermé.`;
                break;

              case VotingWorkflowStatusEnum.VotingSessionStarted:
                message = `La session de vote vient d'être ouverte.`;
                break;

              case VotingWorkflowStatusEnum.VotingSessionEnded:
                message = `La session de vote vient d'être fermé.`;
                break;

              case VotingWorkflowStatusEnum.VotesTallied:
                message = `Les votes ont été comptabilisés.`;
                break;

              default:
                break;
            }
            break;

          case 'ProposalRegistered':
            message = `Une proposition a été ajouté (Proposition n°${ votingContractEvent.data.proposalId }).`;
            break;

          case 'Voted':
            message = `${ votingContractEvent.data.voter } a voté pour la proposition n°${ votingContractEvent.data.proposalId }.`;
            break;
        
          default:
            break;
        }

        return (
          <ListGroup.Item>
            <Card styles={{ border: 'none' }}>
              <Card.Title>{mappingTypes[votingContractEvent.type]}</Card.Title>
              <Card.Subtitle>{votingContractEvent.receivedAt.toLocaleDateString()}</Card.Subtitle>
              <Card.Text>{message}</Card.Text>
            </Card>
          </ListGroup.Item>
        );
      })}
    </ListGroup>
  );
}

export default VotingEventsList;
